import React from 'react'
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalBody,
  ModalCloseButton,
  Heading,
  Box,
} from '@chakra-ui/react'

const steps = [
  'Enter Your Options  Type in the choices you cant decide between.',

  'Spin the Wheel  Tap the button and watch the wheel spin.',
  'Get Your Answer  Let the app pick one for you instantly.',
]

const IntroductionModal = ({ isOpen, onClose }) => {
  return (
    <Modal isCentered isOpen={isOpen} onClose={onClose}>
      <ModalOverlay />
      <ModalContent maxW={'30em'} width={'90%'} mx="auto">
        <ModalCloseButton />
        <ModalBody p="2em">
          <Heading textAlign={'center'}>How to Use</Heading>
          <Box as="ol" mt="1em">
            {steps.map((step, i) => (
              <li key={i}>{step}</li>
            ))}
          </Box>
        </ModalBody>
      </ModalContent>
    </Modal>
  )
}

export default IntroductionModal
